@import 'mixins';

.clients {
  background-color: white;

  .title {
    @include tablet-screen {
      @include font(30px, 600);
    }
  }

  .subtitle {
    @include tablet-screen {
      @include font(20px, 300);
    }
  }

  .clients-container {
    .clients-block {
      display: grid;
      grid-template-columns: 560px 500px;
      grid-column-gap: 20px;
      margin-top: 120px;
      align-items: center;

      @include tablet-screen {
        grid-template-columns: 100%;
        grid-column-gap: 0;
        grid-row-gap: 20px;
        margin-top: 80px;
      }

      &:nth-child(2n) {
        .title {
          grid-column: 1;
        }

        .description {
          grid-column: 1;

        }

        .image {
          grid-column: 2;

          @include tablet-screen {
            grid-column: 1;
          }

        }

        .button {
          grid-column: 1;
        }
      }

      .title {
        @include font(26px, 600);
        line-height: 1.23;
        grid-column: 2;

        @include tablet-screen {
          @include font(22px, 600);
          grid-column: 1;
        }
      }

      .description {
        @include font(18px, 300);
        line-height: 1.55;
        grid-column: 2;

        @include tablet-screen {
          @include font(14px, 300);
          grid-column: 1;
        }

      }

      .image {
        grid-column: 1;
        grid-row: 4 / 1;

        img {
          max-width: 100%;
        }

        @include tablet-screen {
          grid-row: 1;
        }
      }

      .button {
        grid-column: 2;
        width: 246px;

        @include tablet-screen {
          grid-column: 1;
          width: 186px;
        }
      }
    }
  }
}
