.intro {
  height: 100vh;
  align-items: center;
  text-align: center;
  background-image: url("../../assets/img/background/main-bg.webp");
  background-size: cover;
  background-position: top;

  .overlay {
    @include size(100%, 100%);
    background-color: rgba(0, 0, 0, .7);
    position: relative;

    .intro-wrapper {
      margin: 0;
      top: 50%;
      left: 50%;
      position: absolute;
      transform: translate(-50%, -50%);

      @include tablet-screen {
        width: 100%;
      }

      .title {
        padding: 24px 0;
        font-size: 72px;
        font-weight: bold;
        max-width: 960px;

        @include tablet-screen {
          @include font(30px, 600);
          padding-inline: 10px;
          letter-spacing: 1px;
        }
      }

      .description {
        padding: 15px 0 30px;
        font-size: 22px;
        max-width: 960px;

        @include tablet-screen {
          @include font(14px, 300);
          padding-inline: 10px;
        }
      }

      .button-wrapper {
        margin-top: 44px;
      }
      
      .button {
        text-transform: uppercase;
      }
    }
  }
}
